import request, { UPLOAD_URL } from '@/api/index'

export const useUpload = (opts) => {
	const {
		uploadPath,
		tempFilePaths,
		FormData,
		file
	} = opts;
	
				// const body = {
				// 	"output": "json2"
				// };
	const upload = () => {
		return new Promise(async (resolve, reject) => {
			uni.showLoading({
				title: '',
				mask: true
			})
			uni.uploadFile({
				url: request.UPLOAD_URL + uploadPath,
				name: 'file',
				file: file,
				// FormData.output: 'json2',
				filePath: tempFilePaths,
				formData: { output: 'json2'},
				header: {
					// 'token': getToken()
				},
				success: (res) => {
					uni.showToast({
						title: '',
					});
					uni.hideLoading();
					resolve(res?.data)
				},
				fail: (err) => {
					console.log(err);
					uni.showToast({
						title: '',
						icon: 'error'
					});
					uni.hideLoading();
				}
			})
		})
	}

	return {
		upload
	}
}